<template>
  <div>
    <b-card
      class="card-custom card-stretch gutter-b"
      v-for="(trans, trans_index) in trans_info_list"
      v-bind:key="trans_index"
    >
      <!-- <div class="display-4 text-weight-bold text-dark">
        {{ trans_name_list[trans_index] }}
      </div> -->
      <span class="card-label font-weight-bolder text-dark h2">
        {{ trans_name_list[trans_index] }}<br
      /></span>

      <b-card-text
        class="font-weight-bolder text-muted"
        v-bind:key="'contributor_index' + contributor_index"
        v-for="(contributor, contributor_index) in trans.contributor"
      >
        contributor: {{ contributor.name }}
      </b-card-text>
      <b-card-text class="font-size-lg">
        {{ trans.desc }}
      </b-card-text>

      <template v-for="(example, example_index) in trans.examples">
        <b-card-text v-bind:key="'example_' + example_index">
          <!-- <div class="h2">
          Example {{ example_index + 1 }}:</div> -->
          <span
            class="label label-inline label-pill label-primary label-rounded mb-3"
          >
            Example {{ example_index + 1 }}
          </span>
          <div class="font-size-lg font-weight-bold">
            <p>
              <span style="color: rgba(0, 143, 251, 0.85)">ori: </span>
              {{ example.ori }}
            </p>

            <!-- <p class="text-primary font-weight-bolder text-center">|<br/>></p> -->

            <p>
              <span style="color: rgba(0, 143, 251, 0.85)">trans: </span>
              {{ example.trans }}
            </p>
          </div>
        </b-card-text>
      </template>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "widget-transformations",
  data() {
    var trans_info_list = new Array();
    var trans_name_list = new Array();
    for (var trans_index in this.$store.state.taskinfo.available_domain) {
      var trans_name = this.$store.state.taskinfo.available_domain[trans_index];
      // console.log(trans_name);
      try {
        var trans_info = require("../../../../../public/Tasks/" +
          this.$store.state.taskinfo.name +
          "/transformations/" +
          trans_name +
          ".json");
        trans_info_list.push(trans_info);
        trans_name_list.push(trans_name);
      } catch (err) {
        continue;
        // console.log(err);
        // trans_info_list.push({});
      }
    }
    return {
      trans_info_list: trans_info_list,
      trans_name_list: trans_name_list,
    };
  },
};
</script>
